:root{
    --background-color: #fff5fc;
    
    
    --primary:#8c1155;
    
  }
  .primary{
    color: var(--primary);
  
  }
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  ul{
    list-style-type: none;
  }
  a{
    text-decoration: none;
    color: #333;
  }
  .container{
    max-width: 1240px;
    margin: auto;
  }
  body {
    
    font-family: 'poppins', 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  h1{
    font-size: 2.4rem;
    line-height: 1.2;
  }
  h2{
    font-size: 2.4rem;
    line-height: 1.2;
  }
  h5{
    font-size: 1.1rem;
    line-height: 1.2;
  }
  p{
    font-size: 1.2rem;
  }
  .btn{
    padding: 14px 32px;
    border: 1px solid var(--primary);
    background-color: var(--primary);
    color: #fff;
    border-radius: 24px 4px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
  }
  .btn:hover{
    box-shadow: rgb(0, 0, 0 / 15%) 0px 8px 24px;
  }
  input{
    padding: 12px 32px;
    border: 1px solid var(--primary);
    background: transparent;
    border-radius: 24px 4px;
    font-size: 1rem;
    margin-right: .8rem;
    font-family: 'poppins',sans-serif;
  
  }
  @media screen and (max-width:768px){
    h1{
      font-size: 2.4rem;
      line-height: 1.2;
    }
    h2{
      font-size: 2.4rem;
      line-height: 1.2;
    }
    h5{
      font-size: 1.1rem;
      line-height: 1.2;
    }
    p{
      font-size: 1rem;
    }
    .btn{
      width: 100%;
      margin: 1rem 0;
  
    }
    input{
      width: 100%;
  
    }
    
    
  }